html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
}

a {
  text-decoration: none;
}

a:active,
a:hover {
  outline: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus {
  outline: 0;
}

img,
audio,
video {
  max-width: 100%;
  height: auto;
}

audio,
canvas,
iframe,
video,
img,
svg {
  vertical-align: middle;
}

iframe {
  border: 0;
}

textarea {
  resize: none;
  /* remove the resize handle on the bottom right */
  overflow: auto;
  vertical-align: top;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
}

input,
textarea,
select,
button {
  outline: none;
  border: none;
  font-size: 100%;
  margin: 0;
}

button,
input {
  line-height: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
  text-align: left;
}

html,
body {
  font: 400 10px/1.5 Ubuntu, Helvetica, Arial, sans-serif;
  color: #313233;
  background: #f9f9f9;
}

body,
input,
textarea,
button {
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.6rem;
}

h3 {
  font-size: 1.4rem;
}

h4 {
  font-size: 1.25rem;
}

h5 {
  font-size: 1.15rem;
}

p,
li,
em,
b,
a,
details,
summary,
span {
  font-size: 1rem;
}

p,
span,
label,
input,
textarea {
  font-size: 0.8rem;
  color: #727272;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  font-weight: 500;
}

h2,
h3 {
  margin: 0.65em;
}

p {
  margin: 2.2em 0 0.75em 0;
  line-height: 1.6;
}

a,
a:hover {
  text-decoration: none;
}

.container {
  width: 60vw;
  margin: 0 auto;
}

.wrapper {
  display: flex;
}

.bg-alt {
  background: #eeeff1;
}

.inverted {
  background: #34547a;
}

.iconcards {
  font-size: 2rem;
}

.btn {
  background: #34547a;
  text-transform: uppercase;
  color: #f9f9f9;
  padding: 0.8rem 2.425rem;
  letter-spacing: 0.1em;
  font-size: 0.7rem;
  line-height: 1.65;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 5em 1.25em;
}

.logo {
  font: 5.77em Revalia;
  color: #34547a;
}

.menu {
  margin: 1rem -0.4em;
}

.menu a {
  margin: 1.3em;
  text-transform: uppercase;
  color: #000;
  font-size: 0.8rem;
  letter-spacing: 0.109em;
}

.menu a:first-child {
  color: #34547a;
  font-weight: 500;
}

header {
  padding-bottom: 6.5em;
}

.hero {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 3rem 3.6em;
}

.hero .left {
  padding: 0 2rem;
}

.hero .left img {
  margin-bottom: 1.5rem;
}

.hero .right {
  width: 50%;
  text-align: left;
  line-height: 1.6;
  hyphens: none;
}

.hero .right h1 {
  font-weight: 500;
  padding: 0.325em 2rem;
  font-size: 2.4rem;
}

.hero .right p {
  margin: 1rem 2rem;
  font-size: 1.6em;
  line-height: 1.6;
  color: #727272;
}

.hero .right button {
  margin: 2.1em 2.05rem;
  line-height: 1.625;
  font-size: 0.8rem;
  color: #f9f9f9;
  padding: 1.22em 2.6em;
}

.about-me {
  max-width: 50%;
  text-align: center;
  margin: 0 auto;
  padding: 4rem 0;
}

.projects {
  padding: 5rem 2em 5rem 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.projects img {
  margin: 0 2em;
  max-height: 2.5rem;
}

.info {
  padding: 11.9em 0 4.25rem;
  display: flex;
  justify-content: space-between;
  flex-flow: row nowrap;
  text-align: left;
}

.info h2 {
  margin: 4rem 0.05em 0;
}

.bars {
  margin: 2rem 0 0 0;
}

.bars p {
  margin: 0 0 2em;
  line-height: 1.15;
}

.bars p label {
  padding-left: 1px;
  margin-left: 2px;
}

.bars progress {
  padding: 0 1px;
  background: #c4c4c4;
  height: 0.25em;
  width: 22.5rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  height: 0.3em;
}

.bars progress[value]::-webkit-progress-value,
.bars progress[value]::-moz-progress-bar {
  background: #34547a;
  padding: 0 1px;
}

.photo {
  width: 48.2%;
}

.photo img {
  position: relative;
  top: -17px;
}

.workflow {
  padding: 4rem 1rem 13.5em;
}

.workflow .text {
  width: 50%;
  margin: 0 auto;
}

.workflow .text h2 {
  margin: 0.7rem;
}

.workflow .video {
  position: relative;
  top: 1.8rem;
}

.workflow .video .play {
  position: absolute;
  top: calc(50% - 50px);
  margin: 0 auto;
  left: calc(50% - 50px);
  z-index: 5;
}

.gallery {
  padding: 2.8em 0;
}

.gallery img {
  width: 25vw;
  overflow: hidden;
}

.gallery .row1 {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
}

.gallery .view {
  position: absolute;
  width: 25%;
  height: 100%;
  background: rgba(52,84,122,0.8) url("/img/view.svg") no-repeat center;
  left: 25%;
  top: 0;
  z-index: 1;
}

.row2 {
  display: flex;
  flex-flow: row-reverse nowrap;
  justify-content: flex-end;
}

.brand {
  padding: 0 0 5.7em;
}

.brand .mslogo {
  padding: 2.175rem 1.2em;
  display: flex;
  justify-content: space-between;
}

.brand .mslogo img {
  width: 24.177%;
  padding: 0.1em 1em;
}

.get-site {
  max-width: 47%;
  margin: 0 auto;
  padding: 7em 0 calc(5rem -1px);
}

.get-site h2 {
  margin-top: 1.25rem;
  margin-right: 0.77em;
}

.get-site button {
  letter-spacing: 0.15em;
  padding: 0.8rem 2.25rem;
}

.get-site form {
  display: flex;
  flex-flow: column;
  margin: 3.65em 0 5.3em;
}

.get-site .input {
  display: flex;
  justify-content: space-between;
  flex-flow: row;
  margin: 1rem 0;
}

.get-site input,
.get-site textarea {
  font-size: 1.4em;
  text-align: left;
  padding: 1.25em 4.5em 1.25em 1.5em;
  color: #5f5d5d;
}

.footer {
  color: #fff;
  display: flex;
  justify-content: space-between;
  padding: calc(2rem -1px) 0;
}

.footer a,
.footer h3,
.footer span {
  color: #fff;
  font-size: 0.65rem;
}

.footer .left {
  text-align: left;
  padding: 0 22px;
}

.footer .left h3 {
  font-weight: 500;
  margin: 0.37rem 0 0.5em;
  font-size: 1rem;
  letter-spacing: 0.03em;
}

.footer .right img {
  width: 3.6em;
  margin: 1.2em 0.3em 1.2em 1.15em;
}

.caption {
  background: rgba(25,25,25,0.85);
}

.caption span,
.caption a {
  color: #fff;
}

@media (min-width: 320px) {
  html {
    font-size: 14px;
  }
}

@media (min-width: 480px) {
  html {
    font-size: 16px;
  }
}

@media (min-width: 640px) {
  html {
    font-size: 16.5px;
  }
}

@media (min-width: 768px) {
  html {
    font-size: 17px;
  }
}

@media (min-width: 992px) {
  html {
    font-size: 17.5px;
  }
}

@media (min-width: 1200px) {
  html {
    font-size: 18.5px;
  }
}

@media (min-width: 1920px) {
  html {
    font-size: 20px;
  }
}

@media (min-width: 2560px) {
  html {
    font-size: 22px;
  }
}

@media (min-width: 3060px) {
  html {
    font-size: 27.5px;
  }
}